//
const getAreaListService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/app/owner/homePage/getAreaList.do', { data: JSON.stringify(reqData) }, true, 'homeUrl', false).then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

